ï»¿html {
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background-color: $color-prim;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $color-sec;
    // border-radius: 10px;
  }

  overflow-y: hidden;
  overflow-x: hidden;

  scrollbar-width: thin;
  scrollbar-color: rgba($color-sec, 1) rgba($color-prim, 1);
}

body {
  font-family: "Cairo", sans-serif;

  @include LTR {
    text-align: right;
  }

  @include Sm {
    text-align: center;
  }
}

* {
  font-family: inherit;
}

input,
a,
button {
  font-family: "Cairo", sans-serif;

  &:focus {
    outline: none;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
  }
}

button {
  cursor: pointer;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}
