.Flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.after {
  position: relative;

  &:after {
    content: "";
    width: 0;
    height: 0;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    border-bottom: 5px solid $blue;
    display: block;
    position: absolute;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
  }

  &:before {
    content: "";
    display: block;
    width: 47px;
    height: 3px;
    background-color: $blue;
    position: absolute;
    bottom: -22px;
    left: 50%;
    transform: translateX(-50%);
  }
}
@keyframes ani {
  from {
    -webkit-mask-position: 0 0;
    mask-position: 0 0;
  }

  to {
    -webkit-mask-position: 100% 0;
    mask-position: 100% 0;
  }
}

@keyframes ani2 {
  from {
    -webkit-mask-position: 100% 0;
    mask-position: 100% 0;
  }

  to {
    -webkit-mask-position: 0 0;
    mask-position: 0 0;
  }
}
