@import "main";
// End Of Import

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @extend .Flex;
  background-color: $blue;
  z-index: 999999;

  svg {
    width: 100%;
    height: 100%;
    // visibility: hidden;
  }

  // img {
  //   animation: pulse 2s linear infinite;
  // }
}

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba($color-sec, 0.95);
  z-index: 9;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;

  &.open {
    opacity: 1;
    visibility: visible;
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: $color-sec;
  z-index: 99;
  overflow-y: auto;
  transition: 0.5s;
  @extend .Flex;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);

  html[dir="ltr"] & {
    transform-origin: left;
  }

  &.open {
    // transform-origin: left;
    transform: scaleX(1);

    ul {
      opacity: 1;

      li {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &.origin {
    transform-origin: left;

    html[dir="ltr"] & {
      transform-origin: right;
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;

    @include mob480 {
      padding: 0 40px;
      top: 30px;
    }

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #fff;
      // width: 32px;
      // height: 32px;
      // border-radius: 50%;
      // background-color: $color-prim;
      // border: 1px solid #fff;
    }

    .close1 {
      img {
        filter: brightness(0) invert(1);
      }
    }
  }

  & > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-track {
      background-color: $color-sec;
    }

    &::-webkit-scrollbar-thumb {
      // border-radius: 10px;
      background-color: $color-prim;
    }

    @include mob480 {
      margin-top: 50px;
    }

    & > li {
      position: relative;
      padding: 7px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(-10px);

      @include LTR {
        transform: translateX(10px);
      }

      opacity: 0;

      &:nth-child(1) {
        transition-delay: 0.2s;
      }

      &:nth-child(2) {
        transition-delay: 0.4s;
      }

      &:nth-child(3) {
        transition-delay: 0.6s;
      }

      &:nth-child(4) {
        transition-delay: 0.8s;
      }

      &:nth-child(5) {
        transition-delay: 1s;
      }

      &:nth-child(6) {
        transition-delay: 1.2s;
      }

      &:nth-child(7) {
        transition-delay: 1.4s;
      }

      &:nth-child(8) {
        transition-delay: 1.6s;
      }

      &:nth-child(9) {
        transition-delay: 1.8s;
      }

      &:nth-child(10) {
        transition-delay: 2s;
      }

      &:nth-child(11) {
        transition-delay: 2.2s;
      }

      a {
        width: 100%;
        display: block;
        font-size: 18px;
        color: $color-white;
        transition: 0.5s;
      }

      i {
        position: absolute;
        top: 13px;
        left: 13px;
        transform: rotate(90deg);
        font-size: 9px;
        color: currentColor;
        transition: 0.5s;

        html[dir="ltr"] & {
          left: unset;
          right: 13px;
        }

        &.open {
          transform: rotate(0deg);
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }
}

@media (min-width: 1700px) {
  .container {
    max-width: 1640px;
  }
}

.search-pop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  z-index: 999;
  .close {
    position: absolute;
    top: 136px;
    right: 144px;
    @include LTR {
      left: 144px;
      right: unset;
    }
    width: 50px;
    height: 50px;
    // background-color: #fff;
    // border: 1px solid #151a2e;
    border-radius: 6px;
    font-size: 31px;
    @extend .Flex;
    transition: .5s;

    img{
      transition: .5s;
    }

    &:hover{
      background-color: #0098b7;

      img{
        transform: rotate(360deg);
        filter: brightness(0) invert(1);
      }
    }
  }
  .search-form {
    width: 40%;
    height: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    input {
      transition: .5s;
      width: 100%;
      height: 100%;
      background-color: #fff;
      border: 1px solid #051a36;
      border-radius: 6px;
      margin: 0 19px 0 0;
      @include LTR {
        margin: 0 0 0 19px;
      }
      padding: 0 40px;
      font-size: 30px;
      font-weight: 400;
      
      font-size: 25px;
      color: #051a36;
      &::placeholder {
        transition: .5s;
        font-weight: 600;
        font-size: 20px;
        color: #051a36;
      }
      &:focus{
        border-color: #0098b7;

        &::placeholder{
          opacity: .5;
        }
      }
    }
    button {
      width: 180px;
      height: 100%;
      border: none;
      border-radius: 6px;
      background-color: #0098b7;
      color: #fff;

      img {
        transform: scale(0.4);
      }
    }
  }
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;

  &.open {
    opacity: 1;
    visibility: visible;
  }
}

// Header
.topNav {
  background-color: #fff;
  padding: 27px 0 19px;

  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .right {
    display: flex;
    align-items: center;
    .menu-icon {
      display: none;
    }
    ul {
      display: flex;
      align-items: center;

      li {
        @include Md {
          margin-right: 35px;
          @include LTR {
            margin-left: 35px;
            margin-right: 0;
          }
        }
        @include Xl {
          margin-right: 63px;
          @include LTR {
            margin-left: 63px;
            margin-right: 0;
          }
        }

        a {
          font-size: 15px;
          font-weight: 600;
          color: #051a36;
          padding-bottom: 15px;
          position: relative;

          &:after {
            content: "";
            width: 0;
            height: 0;
            border-right: 5px solid transparent;
            border-left: 5px solid transparent;
            border-bottom: 5px solid $blue;
            display: block;
            position: absolute;
            bottom: -6px;
            left: 50%;
            transform: translateX(-50%);
            transition: 0.5s;
            opacity: 0;
            visibility: hidden;
          }

          &:before {
            content: "";
            display: block;
            width: 100%;
            height: 3px;
            background-color: $blue;
            position: absolute;
            bottom: -8px;
            left: 0;
            transition: 0.5s;
            opacity: 0;
            visibility: hidden;
          }

          &:hover,
          &.active {
            &:after {
              bottom: 0;
              opacity: 1;
              visibility: visible;
            }
            &:before {
              bottom: -2px;
              opacity: 1;
              visibility: visible;
            }
          }
        }
      }
    }

    .searchTriger {
      width: 43px;
      height: 35px;
      cursor: pointer;
      border: 3px solid #0098b7;
      transition: .5s;
      &:hover{
        border-color: #151a2e;
      }
      @extend .Flex;
      @include Md {
        margin-right: 35px;
        @include LTR {
          margin-left: 35px;
          margin-right: 0;
        }
      }
      @include Xl {
        margin-right: 63px;
        @include LTR {
          margin-left: 63px;
          margin-right: 0;
        }
      }
    }

    .lang-switcher {
      width: 98px;
      height: 44px;
      border-radius: 6px;
      border: 0.5px solid #051a36;
      @extend .Flex;
      cursor: pointer;
      position: relative;
      transition: .5s;

      a{
        font-size: 15px;
        font-weight: 600;
        color: #051a36;
      }

      &:hover {
        border: 1px solid #0098b7;
      }
    }
  }
}

header {
  background-color: $blue;

  .swiper-container {
    overflow: hidden;

    .swiper-slide {
      display: flex;
      align-items: center;
      justify-content: center;

      .row {
        width: 80%;

        @include Sm {
          width: 80%;
        }

        @include Xs {
          width: 100%;
        }
      }
      .col-lg-6 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: baseline;

        &:first-child {
          @include Xs {
            order: 5;
            margin-top: 20px;
          }
        }

        > img {
          display: block;
          margin: auto;
          filter: blur(20px);
          transform: scale(1.2);
          @include Xs {
            transform: none;
            width: 210px;
          }
          transition: 0.5s 0.5s;
          opacity: 0;
        }

        @include Xs {
          align-items: center;
        }

        span {
          font-size: 17px;
          @include med {
            font-size: 15px;
          }
          font-weight: 400;
          text-transform: uppercase;
          color: #fff;
          transition: 0.5s 0.5s;
          opacity: 0;
          transform: translateX(50px);
          filter: blur(10px);
          @include LTR {
            transform: translateX(-50px);
          }
        }

        h2 {
          margin: 20px 0;
          font-size: 60px;
          @include med {
            font-size: 50px;
          }
          font-weight: 800;
          color: $text;
          transform: translateX(50px);
          filter: blur(10px);
          @include LTR {
            transform: translateX(-50px);
          }
          transition: 0.5s 0.7s;
          opacity: 0;
        }

        pre {
          @include med {
            max-width: 100%;
            white-space: normal;
            margin-bottom: 20px;
          }
          font-size: 19px;
          @include med {
            font-size: 16px;
          }
          font-weight: 400;
          line-height: 30px;
          color: #fff;
          margin-bottom: 123px;
          transform: translateX(50px);
          filter: blur(10px);
          @include LTR {
            transform: translateX(-50px);
          }
          transition: 0.5s 0.9s;
          opacity: 0;
          max-width: 100%;
          white-space: normal;
        }

        .links {
          display: flex;
          align-items: center;
          transform: translateX(50px);
          filter: blur(10px);
          @include LTR {
            transform: translateX(-50px);
          }
          transition: 0.5s 1.1s;
          opacity: 0;
          a {
            width: 126px;
            height: 56px;
            border-radius: 6px;
            border: 1px solid transparent;
            transition: 0.5s;
            @extend .Flex;

            &:first-child {
              background-color: #fff;
              color: $blue;
              font-size: 12px;
              font-weight: 400;
              @include LTR {
              }
              box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.16);
              transition: 0.5s;

              &:hover {
                opacity: .7;
              }
            }

            &:last-child {
              margin-left: 21px;
              border-color: $text;
              color: $text;
              font-size: 12px;
              font-weight: 400;
              @include LTR {
                margin-right: 21px;
                margin-left: 0;
              }
              transition: 0.5s;

              &:hover {
                color: #FFF;
                border-color: #FFF;
              }
            }
          }
        }
      }

      &.swiper-slide-active {
        .col-lg-6 {
          > img {
            transform: none !important;
            filter: none !important;
            opacity: 1 !important;

            @include Xs {
              width: 210px !important;
            }
          }
        }
        span,
        h2,
        pre,
        .links {
          transform: none !important;
          filter: none !important;
          opacity: 1 !important;
        }
      }
    }

    .swiper-pagination {
      bottom: 60px;
      @include med {
        bottom: 20px;
      }

      @include Xs {
        bottom: -6px;
      }

      span {
        width: 12px;
        height: 12px;
        border: 1px solid #fff;
        background-color: transparent;
        opacity: 1;
        &:focus {
          outline: none;
        }

        &.swiper-pagination-bullet-active {
          background-color: #fff;
        }
      }
    }

    [class^="swiper-button-"] {
      background-image: none;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      border: 1.5px solid #FFF;
      @include med {
        width: 50px;
        height: 50px;
      }
      // &:after {
      //   display: none !important;
      // }
      transition: .5s;
      // filter: brightness(0) invert(1);

      &:after{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(.5);
        opacity: 0;
        visibility: hidden;
        transition: .3s;
        z-index: -1;
      }

      &:focus{
        outline: none;
      }

      &:hover{
        // background-color: #FFF;
        // filter: none;

        &:after{
          transform: translate(-50%, -50%) scale(.9);
          opacity: .6;
          visibility: visible;
        }
      }

      img{
        transition: .5s;
      }
    }

    .swiper-button-next {
      &:hover{
        img{
          transform: translateX(5px);
        }
      }
      @include LTR {
        transform: scaleX(-1);
        &:hover{
          img{
            transform: translateX(5px);
          }
        }
      }
    }

    .swiper-button-prev {
      transform: scaleX(-1);
      &:hover{
        img{
          transform: translateX(5px);
        }
      }

      @include LTR {
        transform: none;
        &:hover{
          img{
            transform: translateX(5px);
          }
        }
      }
    }
  }
}

