        background-size: cover;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    span {
      font-size: 18px;
      font-weight: 400;
      color: $blue;
      text-transform: uppercase;

      @include Xs {
        text-align: center !important;
        display: block;
      }
    }
