      font-size: 40px;
      font-weight: 800;
      color: $text;
      @extend .after;
      margin-bottom: 85px;

      @include med {
        margin-bottom: 50px;
      }

      &:after {
        @include Xl {
          left: 23px;
        }

        @include LTR {
          right: 13px;
          left: unset;
        }

        @include Xs {
          left: 50% !important;
          transform: translateX(-50%) !important;
          right: unset !important;
        }
      }
      &:before {
        @include Xl {
          left: 0;
          transform: unset;
        }

        @include LTR {
          right: 0;
          left: unset;
          transform: none;
        }

        @include Xs {
          left: 50% !important;
          transform: translateX(-50%) !important;
          right: unset !important;
        }
