// Big Boxs

.big-boxs {
  display: flex;

  @include Xs {
    flex-wrap: wrap;
  }

  .box {
    flex-shrink: 0;
    flex-basis: calc(100% / 3);
    @include Xs {
      flex-basis: 100%;
    }
    overflow: hidden;
    position: relative;

    > img{
      transition: .5s;
    }

    .content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#0098b7, 0.45);
      transition: .5s;
      padding: 0 94px 0 140px;
      @include LTR {
        padding: 0 140px 0 94px;
      }
      @include Sm {
        padding: 50px;
      }
      display: flex;
      // align-items: center;
      justify-content: center;
      flex-direction: column;

      &:hover{
        background-color: rgba(#0098b7, .94);
